.footer {
  background-image: url('../images/footer_imager.png');
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;

  height: 511px;

  position: sticky;
  z-index: -1;

  @media (max-width: 744px) {
    height: 480px;
  }

  @media (max-width: 320px) {
    height: 200px;
  }
}
